h2, h4 {
    text-align: left;
}

.card {
    height: 350px;
}

.card-header {
    height: 75px;
}

.card-img-top {
    width: 80%;
    height: 200px;
    margin: auto;
    padding-top: 3%;
}

.card-text {
    text-align: center;
    vertical-align: baseline;
}

.row {
    margin: 2%;
}

.card button {
    background-color: cadetblue;
    border: none;
}

.card button:hover {
    background-color: green;
}

.modal-body {
    font-family: Arial, Helvetica, sans-serif;
}